body {
    background: #333;
}

footer,
header {
    background: #000;
    color: #fff;
}

header {
    height: 50px;
    line-height: 50px;
    position: fixed;
    z-index: 500;
    top: 0;
    left: 0;
    right: 0;
    .container {
        margin-top: 0;
    }
}

footer {
    padding: 32px 20px;
    position: relative;
}

.container {
    width: 1300px;
    margin: 50px auto 0;
    position: relative;
}

.left-catelog {
    width: 208px;
    vertical-align: baseline;
    padding: 20px 12px 60px 36px;
    top: auto;
    position: relative;
    overflow-y: visible;
    display: inline-block;
}

.right-content {
    width: calc(100% - 208px);
    position: absolute;
    vertical-align: baseline;
    background-color: #f4f4f4;
    flex-grow: unset;
    display: inline-block;
    top: 0;
    padding: 8px;
    min-height: 300px;
}

.page-tip {
    border: 1px dashed #97CCFE;
    padding: 10px;
    border-radius: 5px;
    background-color: #F5FAFF;
    position: relative;
    .blue {
        color: #97CCFE;
    }
    ul {
        width: 100%;
        padding: 0;
        li {
            padding-left: 10px;
            list-style: inside;
            a {
                color: #666;
            }
        }
    }
    .toggle {
        position: absolute;
        right: 0;
        top: 10px;
        height: 26px;
        width: 26px;
        &::before {
            font: normal normal normal 14px/1 FontAwesome;
            font-size: 14px;
            font-size: inherit;
            text-rendering: auto;
            content: "\f068";
        }
    }
    &.min {
        width: 200px;
        ul {
            display: none;
        }
        .toggle {
            &::before {
                content: "\f067";
            }
        }
    }
}
.page-footer {
    text-align: center;
}

.zd-tab {
    .zd-tab-head {
        .zd-tab-item {
            width: 130px;
        }
    }
}

.page-header {
    padding: 8px;
    .opt-btn {
        position: absolute;
        right: 0;
        bottom: 0;
    }
}

.dialog-box {
    form {
        min-width: 600px;
    }
}

#debug-box {
    a {
        text-decoration: none;
    }
    .uri-box {
        select,
        input {
            height: 30px;
        }
        input {
            min-width: 300px;
        }
    }
    table {
        input {
            height: 30px;
            width: 100%;
        }
    }
}

.json-box {
    pre {
        outline: 1px solid #ccc;
        padding: 5px;
        margin: 5px;
    }

    .string {
        color: green;
    }

    /*字符串的样式*/

    .number {
        color: darkorange;
    }

    /*数字的样式*/

    .boolean {
        color: blue;
    }

    /*布尔型数据的样式*/

    .null {
        color: magenta;
    }

    /*null值的样式*/

    .key {
        color: red;
    }

    /*key值的样式*/
}